"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import Image from "next/image";

interface SidebarItemProps {
  icon: string; // Ruta del ícono
  label: string; // Texto del ítem
  href: string; // Enlace al que dirige el ítem
}

const SidebarItem: React.FC<SidebarItemProps> = ({ icon, label, href }) => {
  const pathname = usePathname();
  const isActive = pathname === href;

  return (
    <Link href={href} passHref>
      <div
        className={`flex items-center gap-4 p-2 rounded-lg cursor-pointer transition-all`}
        style={{
          color: isActive
            ? "var(--primary-color)" // Texto activo
            : "var(--color-primary-700)", // Texto por defecto
        }}
      >
        {/* Fondo e ícono dinámico */}
        <div
          className={`flex items-center justify-center w-8 h-8 rounded-md transition-all`}
          style={{
            backgroundColor: isActive
              ? "var(--primary-color)" // Fondo del ícono activo
              : "transparent", // Fondo del ícono default
            color: isActive
              ? "var(--color-base-100)" // Ícono blanco en estado activo (Dark Mode)
              : "inherit", // Ícono igual al texto por defecto
          }}
        >
          <Image
            src={icon}
            alt={label}
            width={20}
            height={20}
            style={{
              filter: isActive ? "invert(1)" : "none", // Invertir para blanco en Dark Mode
            }}
          />
        </div>

        {/* Texto */}
        <span
          className={`font-medium transition-all`}
          style={{
            color: isActive
              ? "var(--primary-color)" // Texto activo
              : "var(--color-primary-700)", // Texto default
          }}
        >
          {label}
        </span>
      </div>

      {/* Hover */}
      <style jsx>{`
        div:hover {
          color: var(--color-primary-900); /* Cambia el color del texto en hover */
        }
        div:hover .flex {
          background-color: var(--color-primary-900); /* Cambia el fondo del ícono en hover */
        }
      `}</style>
    </Link>
  );
};

export default SidebarItem;
